import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation, Pagination } from 'swiper/modules';
import { FaCalendarAlt, FaMapMarkerAlt } from 'react-icons/fa';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import './eventos.css';

function EventosEspecialesSlider() {
  const eventos = [
    {
      id: 1,
      titulo: 'Vigilia de Oración',
      fecha: '12 Mayo 2025',
      lugar: 'Piedras Vivas',
      descripcion: 'Una noche de intercesión, alabanza y comunión.',
      imagen: 'https://images.unsplash.com/photo-1579817783579-4870505330bc?fm=jpg&q=60&w=3000&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8N3x8dmlnaWxpYSUyMGRlJTIwcGFzY3VhfGVufDB8fDB8fHww'
    },
    {
      id: 2,
      titulo: 'Campamento de Jóvenes',
      fecha: '25-27 Junio 2025',
      lugar: 'Piedras Vivas',
      descripcion: 'Tres días de amistad, palabra, deportes y fogones.',
      imagen: 'https://images.unsplash.com/photo-1529333166437-7750a6dd5a70?fm=jpg&q=60&w=3000&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxzZWFyY2h8M3x8bG9zJTIwam92ZW5lc3xlbnwwfHwwfHx8MA%3D%3D'
    },
    {
      id: 3,
      titulo: 'Bautismos 2025',
      fecha: '18 Agosto 2025',
      lugar: 'Piedras Vivas',
      descripcion: 'Celebrá con nosotros este momento especial.',
      imagen: 'https://wallpapers.com/images/hd/baptism-background-1920-x-1200-107nxl0l6wyo286u.jpg'
    }
  ];

  return (
    <section id="eventos-especiales" className="eventos-slider-section container-fluid  bg-dark p-0">
      <div className='container shadow-lg py-2 pb-5'>
        <div className="section-header text-center my-4 mb-0">
          <h2 className="section-title text-light">Próximos Eventos</h2>
          <p className="section-subtitle  text-light">Lo que se viene en la iglesia</p>
        </div>

        <Swiper
          modules={[Autoplay, Navigation, Pagination]}
          spaceBetween={0}
          slidesPerView={1}
          loop={true}
          autoplay={{
            delay: 5000,
            disableOnInteraction: false,
          }}
          navigation={true}
          pagination={{ clickable: true }}
          className="eventos-swiper rounded-3"
        >
          {eventos.map(evento => (
            <SwiperSlide key={evento.id}>
              <div
                className="evento-slide"
                style={{ backgroundImage: `url(${evento.imagen})` }}
              >
                <div className="evento-overlay">
                  <h3 className="evento-titulo">{evento.titulo}</h3>
                  <p className="evento-info">
                    <FaCalendarAlt className="me-2" /> {evento.fecha}
                    {'  '}
                    <FaMapMarkerAlt className="ms-3 me-2" /> {evento.lugar}
                  </p>
                  <p className="evento-descripcion">{evento.descripcion}</p>
                </div>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </section>
  );
}

export default EventosEspecialesSlider;
